.arch_item .arch_item_title {
  position: relative;
  color: #242f35;
  padding-left: 16px;
  margin: 30px auto 20px;
  font-weight: 400;
  height: 20px;
  line-height: 20px;
  font-size: 18px;
}
.arch_item .arch_item_title::before {
    content: '';
    display: block;
    position: absolute;
    width: 4px;
    height: 100%;
    top: 0;
    left: 0;
    z-index: -1;
    background: #1a1818;
    background: -webkit-gradient(linear, left top, left bottom, color-stop(35%, #1a1818), to(#353030)) left center no-repeat;
    background: -webkit-linear-gradient(top, #1a1818 35%, #353030 100%) left center no-repeat;
    background: -o-linear-gradient(top, #1a1818 35%, #353030 100%) left center no-repeat;
    background: linear-gradient(to bottom, #1a1818 35%, #353030 100%) left center no-repeat;
    background-size: 100%;
}
.arch_item ul {
  padding-left: 20px;
  font-size: 16px;
}
.arch_item ul a li {
    padding: 10px 0;
    color: #666;
}
.arch_item ul a:hover li {
    color: #353030;
}


